@font-face {
      font-family: "MyriadPro-Semibold-Italic";   /*Can be any text*/
      src: local("MyriadPro-Semibold-Italic"),
      url("../../fonts/MyriadPro-Semibold-Italic.ttf") format("truetype");
}

.chartsWindow {
      background: #EAEAEA;
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-direction: column;
      padding: 35px 50px 45px 50px;
}
.wrapper {
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-direction: column;
      height: 100%;
      width: 100%;
      gap: 32px;
}

.chartCard {
      width: 100%;
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      padding-top: 18px;
      background: #FFFFFF;
      border: 1px solid #A1A2A4;
      border-radius: 4px;
}

.navBar {
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-direction: column;
      width: 100%;
      gap: 15px;
}

.navBarRow {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
}

.navBarButtonRow {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      height: 39px;
      width: 100%;
}


.dropDownWrapper {
      display: flex;
      align-items: center;
      font-family: 'MyriadPro-Semibold-Italic', sans-serif;
      font-size: 16px;
      gap: 6px;
}
.dropDownWrapperText {
      padding-top: 12px;
      padding-bottom: 8px;
}